@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@400;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans&display=swap");
::-webkit-scrollbar {
  width: 0.5rem;
}

::-webkit-scrollbar-thumb {
  background: hsl(217deg, 33%, 17%);
  border-radius: 0.5rem;
}
::-webkit-scrollbar-thumb:hover {
  background: hsla(225deg, 20%, 61%, 0.5);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  list-style: none;
  color: hsl(210deg, 40%, 98%);
  background-color: transparent;
  font-size: 1rem;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Heebo", sans-serif;
  background-color: hsl(222deg, 46%, 11%);
}

img {
  display: block;
}

.main__content {
  display: grid;
  margin: 0 10%;
}

.wrapper {
  position: relative;
}

h1,
h2,
h3 {
  font-weight: 700;
  line-height: 1.1;
  margin-top: 0;
}

p {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.header-main {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 0.5rem 1rem hsla(0deg, 0%, 0%, 0.1);
  align-items: center;
  padding: 0.5rem 5%;
  z-index: 1000;
  transition: all 0.25s ease-in-out;
}
.header-main .left-side {
  display: flex;
  align-items: center;
  gap: 0 5%;
  width: 100%;
}
.header-main .left-side .logo {
  display: flex;
  align-items: center;
  max-width: fit-content;
}
.header-main .left-side .logo .logo-text {
  font-size: 1.5rem;
  margin: 0 0.5rem;
}
.header-main .left-side .line {
  height: 2rem;
  border: 0.08rem solid;
  border-color: hsl(215deg, 20%, 65%);
  background-color: hsl(215deg, 20%, 65%);
}
.header-main .left-side .navbar {
  display: flex;
  align-items: center;
  gap: 0 5%;
  width: 100%;
}
.header-main .left-side .navbar a {
  padding: 0.75rem 0.5rem;
  color: hsl(215deg, 20%, 65%);
}
.header-main .left-side .navbar a:hover {
  color: hsl(210deg, 40%, 98%);
}
.header-main .left-side .navbar .dropdown-resources .dropbtn {
  padding: 0.75rem 0.5rem;
  color: hsl(215deg, 20%, 65%);
}
.header-main .left-side .navbar .dropdown-resources .dropdown-content {
  display: none;
  position: absolute;
  background-color: hsl(222deg, 46%, 11%);
  box-shadow: 0 0.5rem 1rem hsla(0deg, 0%, 0%, 0.1);
  border-radius: 0.5rem;
  z-index: 100;
  width: 7rem;
}
.header-main .left-side .navbar .dropdown-resources .dropdown-content a {
  display: block;
  text-align: left;
}
.header-main .left-side .navbar .dropdown-resources .dropdown-content a:first-child {
  border-radius: 0.5rem 0.5rem 0 0;
}
.header-main .left-side .navbar .dropdown-resources .dropdown-content a:last-child {
  border-radius: 0 0 0.5rem 0.5rem;
}
.header-main .left-side .navbar .dropdown-resources .dropdown-content a:hover {
  box-shadow: 0 0 0.3rem hsla(0deg, 0%, 0%, 0.25), 0 0 0.9rem hsla(0deg, 0%, 0%, 0.165), 0 0 2rem hsla(0deg, 0%, 0%, 0.125), 0 0 6.5rem hsla(0deg, 0%, 0%, 0.085);
}
.header-main .left-side .navbar .dropdown-resources:hover .dropdown-content {
  display: block;
}
.header-main .left-side .navbar .dropdown-resources:hover .dropbtn {
  color: hsl(210deg, 40%, 98%);
}
.header-main .right-side {
  display: flex;
  justify-content: flex-end;
  height: 80%;
  gap: 0 0.5rem;
}
.header-main .right-side .form-search {
  display: flex;
  align-items: center;
  width: 45%;
}
.header-main .right-side .form-search .magnify-icon {
  position: absolute;
  margin-left: 0.5rem;
}
.header-main .right-side .form-search .magnify-icon img {
  width: 70%;
}
.header-main .right-side .form-search #search-input {
  width: 100%;
  height: 100%;
  padding-left: 1.7rem;
  border: 1px solid hsl(215deg, 20%, 65%);
  color: hsl(215deg, 20%, 65%);
  border-radius: 0.5rem;
  transition: 0.2s;
}
.header-main .right-side .form-search #search-input:focus {
  color: hsl(210deg, 40%, 98%);
  background-color: hsl(217deg, 33%, 17%);
}

.header-main--scrolled {
  padding: 0.2rem 5% !important;
  background-color: hsl(222deg, 46%, 11%);
}

.waves {
  position: absolute;
  top: 0;
  left: 0;
  shape-rendering: auto;
  height: 100vh;
  width: 100%;
  opacity: 0.13;
  z-index: -1;
}
.waves__bottom {
  top: auto;
  bottom: 0;
  transform: rotate(180deg);
}
@media (max-width: 768px) {
  .waves {
    height: 80vh;
  }
  .waves .wave__1,
.waves .wave__2,
.waves .wave__3 {
    display: none;
  }
}

.section__container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.1fr 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas: "section__header" ".";
}
.section__container .section__header {
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-template-rows: 1fr;
  gap: 0 0.5rem;
  grid-auto-flow: row;
  grid-template-areas: "section__header-icon section-title see-all-btn";
  grid-area: section__header;
}
.section__container .section__header .section__header-icon {
  grid-area: section__header-icon;
  align-self: center;
}
.section__container .section__header .section__header-icon img {
  height: 2rem;
}
.section__container .section__header .section-title {
  grid-area: section-title;
  align-self: center;
}
.section__container .section__header .section-title h2 {
  font-size: 1.2rem;
}
.section__container .section__header .see-all-btn {
  grid-area: see-all-btn;
  align-self: center;
  width: 100%;
  padding: 0.3rem 0.7rem;
  margin: 0;
  background-color: hsl(217deg, 33%, 17%);
}

.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  text-align: center;
  border-radius: 0.5rem;
  font-size: 1rem;
  cursor: pointer;
}
.btn__gradient {
  background-image: linear-gradient(to right, rgb(71, 118, 230), rgb(142, 84, 233));
}
.btn__default {
  background-color: hsl(214deg, 20%, 34%);
}
.btn:hover, .btn:focus {
  background-image: none;
  background-color: transparent;
  outline: 1px solid hsl(215deg, 20%, 65%);
}

.btn_to_top {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: 100;
  cursor: pointer;
  padding: 1rem;
  border-radius: 1.5rem;
  background-color: hsl(214deg, 20%, 34%);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s;
}
.btn_to_top.active {
  opacity: 1;
  pointer-events: auto;
}
.btn_to_top:hover {
  background-color: hsl(217deg, 33%, 17%);
}

.header-main .right-side .btn {
  position: relative;
  padding: 0 0.9rem;
  font-size: 0.85rem;
}
.header-main .right-side .btn__gradient {
  padding-left: 1.5rem;
}
.header-main .right-side .btn__gradient .wallet-icon {
  position: absolute;
  left: 0.5rem;
}
.header-main .right-side .btn__gradient .wallet-icon img {
  width: 70%;
}

.footer__main {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem 0;
}
.footer__main .column {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}
.footer__main .column__logo {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.footer__main .column__logo_text {
  margin-left: 0.5rem;
  font-size: 1.5rem;
}
.footer__main .column__title {
  font-size: 1.2rem;
  letter-spacing: 0.05rem;
}
.footer__main .column__form {
  position: relative;
  padding-block: 0.1rem;
  padding-right: 3rem;
}
.footer__main .column__form::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-image: linear-gradient(to right, rgb(71, 118, 230), rgb(142, 84, 233));
  border-radius: 0.5rem;
}
.footer__main .column__form::before:focus {
  background-image: none;
}
.footer__main .column__form .form__input {
  padding: 0.75rem 1rem;
  margin-left: 0.1rem;
  border-radius: 0.5rem;
  position: relative;
  background-color: hsl(222deg, 46%, 11%);
}
.footer__main .column__form .plane__icon {
  top: 0;
  position: absolute;
  padding: 1rem;
  cursor: pointer;
}
.footer__main .container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0 5rem;
  height: 60vh;
}
@media (min-width: 768px) {
  .footer__main .container {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .footer__main .container {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__main .container .column {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem 0;
}
.footer__main .container .column__title {
  font-size: 1.2rem;
  letter-spacing: 0.05rem;
}
.footer__main .container .column__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
.footer__main .container .column__links a {
  color: hsl(215deg, 20%, 65%);
}
.footer__main .container .column__links a:hover {
  color: hsl(210deg, 40%, 98%);
}
.footer__main .copyright {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem 0;
  margin-bottom: 2rem;
}
.footer__main .copyright__text, .footer__main .copyright__link {
  font-size: 0.8rem;
  letter-spacing: 0.05rem;
  color: hsl(215deg, 20%, 65%);
}
.footer__main .copyright__link:hover {
  color: hsl(210deg, 40%, 98%);
}
@media (min-width: 768px) {
  .footer__main .copyright {
    flex-direction: row;
    gap: 0 1rem;
  }
  .footer__main .copyright__text, .footer__main .copyright__link {
    font-size: 0.9rem;
  }
}

.hero {
  height: 100vh;
}
.hero .container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3% 0;
  height: calc(100vh - 3rem);
}
.hero .container__title {
  max-width: 25ch;
  font-size: 3rem;
  letter-spacing: 0.1rem;
  text-align: center;
  line-height: 1;
}
.hero .container__subtitle {
  max-width: 60ch;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: hsl(215deg, 20%, 65%);
  text-align: center;
  margin-bottom: 3rem;
}
.hero .container__buttons {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.hero .container__buttons .btn__default .arrow-icon {
  margin: 0 0 0 0.5rem;
}

.collections .collections-category {
  margin-top: 2rem;
  display: grid;
  gap: 2rem 2%;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}
.collections .collections-category .collections-category-card {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  position: relative;
}
.collections .collections-category .collections-category-card img {
  border-radius: 0.5rem;
  width: 100%;
  cursor: pointer;
}
.collections .collections-category .collections-category-card .card-text {
  position: absolute;
  background-color: hsl(222deg, 46%, 11%);
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0.5rem 2rem;
  margin: 2rem 0;
  border-radius: 0.5rem;
}

@media (max-width: 576px) {
  .header-main {
    display: flex;
  }
  .header-main .left-side .line {
    display: none;
  }
  .header-main .left-side .navbar {
    display: none;
  }
  .header-main .right-side {
    display: none;
  }

  .hero .hero__container {
    margin-top: -5rem;
  }
  .hero .hero__container .hero__title {
    font-size: 1.7rem;
    line-height: 1.2;
  }
  .hero .hero__container .hero__buttons {
    margin-top: 1rem;
  }

  .collections .collections__container .collections-category {
    gap: 0 0;
  }
  .collections .collections__container .collections-category .collections-category-item:not(:first-child) {
    width: 100%;
    display: none;
  }
}
@media (max-width: 992px) {
  .header-main {
    padding: 0.7rem !important;
  }
  .header-main .left-side .navbar {
    display: none;
  }
}